let wait = 60;
export default {
  methods: {
    // 原生js写法
    getCodeTime (evt) {
      if(wait == 0){
        evt.target.removeAttribute('disabled')
        evt.target.innerText = '重新获取' ;
        wait = 60
      }
      else{
        evt.target.setAttribute('disabled', true)
        evt.target.innerText = wait + '秒后重试'
        wait--;
        let timer = setTimeout(() => {
          this.getCodeTime(evt)
        }, 1000)
      }
    }
  }
}



