<template>
  <div class="rd-field" v-clickoutside="doCloseActive">
    <span class="rd-label" v-text="label"></span>
    <input
      ref="input"
      class="rd-field-core"
      :class="{'reset': specPwd}"
      :placeholder="placeholder"
      :name = "name"
      :number="type === 'number'"
      :type="type"
      @focus="active = true"
      :disabled="disabled"
      :readonly="readonly"
      :value="currentValue"
      @input="handleInput">
    <div
      @click="handleClear"
      class="rd-field-clear"
      :class="{'pos-right': specPwd}"
      v-if="!disableClear"
      v-show="currentValue && type !== 'textarea' && active">
      <i class="mintui mintui-field-error"></i>
    </div>
    <span class="rd-field-state" v-if="state" :class="['is-' + state]">
      <i class="mintui" :class="['mintui-field-' + state]"></i>
    </span>
    <div class="rd-field-other">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import XCell from './Cell.vue';
  import Clickoutside from '../directives/clickOutside';
  /**
   * mt-field
   * @desc 编辑器，依赖 cell
   * @module components/field
   *
   * @param {string} [type=text] - field 类型，接受 text, textarea 等
   * @param {string} [label] - 标签
   * @param {string} [rows] - textarea 的 rows
   * @param {string} [placeholder] - placeholder
   * @param {string} [disabled] - disabled
   * @param {string} [readonly] - readonly
   * @param {string} [state] - 表单校验状态样式，接受 error, warning, success
   *
   * @example
   * <mt-field v-model="value" label="用户名"></mt-field>
   * <mt-field v-model="value" label="密码" placeholder="请输入密码"></mt-field>
   * <mt-field v-model="value" label="自我介绍" placeholder="自我介绍" type="textarea" rows="4"></mt-field>
   * <mt-field v-model="value" label="邮箱" placeholder="成功状态" state="success"></mt-field>
   */
  export default {
    name: 'rd-field',
    data() {
      return {
        active: false,
        currentValue: this.value
      };
    },
    directives: {
      Clickoutside
    },
    props: {
      type: {
        type: String,
        default: 'text'
      },
      rows: String,
      label: String,
      placeholder: String,
      name: String,
      readonly: Boolean,
      disabled: Boolean,
      disableClear: Boolean,
      state: {
        type: String,
        default: 'default'
      },
      value: {},
      attr: Object,
      specPwd:{
        type: Boolean,
        default: false
      }
    },
    components: { XCell },
    methods: {
      doCloseActive() {
        this.active = false;
      },

      handleInput(evt) {
        let formatValue = evt.target.value;
        if(this.type == 'tel'){
          formatValue = formatValue.replace(/\D*$/,'')
        }
        this.$refs.input.value = formatValue
        this.currentValue = formatValue;
      },

      handleClear() {
        if (this.disabled || this.readonly) return;
        this.currentValue = '';
        this.$refs.input.focus()
      }
    },

    watch: {
      value(val) {
        this.currentValue = val;
      },
      currentValue(val) {
        this.$emit('input', val)
      },
      attr: {
        immediate: true,
        handler(attrs) {
          this.$nextTick(() => {
            if(this.type == 'tel'){
              this.$refs.input.setAttribute('maxlength', 11)
            }
            const target = [this.$refs.input, this.$refs.textarea];
            target.forEach(el => {
              if (!el || !attrs) return;
              Object.keys(attrs).map(name => el.setAttribute(name, attrs[name]));
            });
          });
        }
      }
    }
  };
</script>

<style>
  @component-namespace rd {
    @component field {
      /*display: flex;*/
      @when textarea {
        align-items: inherit;
        .rd-cell-value {
          padding: 5px 0;
        }
      }
      .rd-label {
        display:inline-block;
        padding-left:15px;
      }
      .rd-cell-value {
        /*flex: 1;*/
        color: inherit;
      }
      .rd-field-core {
        -webkit-appearance: none;
        border-radius: 0;
        border: 0;
        outline: 0;
        width: 65%;
        position: absolute;
        left: 25%;
        top:.12rem;
      }
      .reset.rd-field-core {width: 62%;}
      @descendent clear {
        opacity: .2;
        position: absolute;
        right: 15px;
        top: 3px;
      }
      .pos-right { right: 35px;}
      @descendent state {
        color: inherit;
        margin-left: 20px;
        .mintui {
          font-size: 20px;
        }
        @when error {
          color: $error-color;
        }

        @when warning {
          color: $warning-color;
        }

        @when success {
          color: $success-color;
        }

        @when default {
          margin-left: 0;
        }
      }
      @descendent other {
        position: absolute 0 0 * *;
      }
    }
  }
</style>



